        this.scrolled = false;
      } else this.scrolled = true;
    },
  },
  watch: {
    doSomething() {
      if (this.scrolled === true) {
        document.body.classList.add("hidden");
        return this.scrolled;
      } else {
        document.body.classList.remove("hidden");
        return this.scrolled;
      }
    },
  },
  computed: {
